<template>
  <div :style='{"width":"100%","padding":"30px 7% 40px","margin":"0px auto","position":"relative","background":"#fff"}'>
    <div class="section-title" :style='{"margin":"10px 0","color":"#fff","textAlign":"center","background":"#333","width":"100%","fontSize":"20px","lineHeight":"54px"}'>{{totalPrice>0?'':''}}</div>
    <el-descriptions v-if="seat!=1" title=""></el-descriptions>
    <el-table v-if="seat!=1"
      :data="addressList"
      style="width: 100%;padding: 0 10px;">
      <el-table-column
        label=""
        width="55">
        <template slot-scope="scope">
          <el-radio
            v-model="radio"
            :label="scope.$index"
            @change.native="getCurrentRow(scope.$index)">
            &nbsp;</el-radio>
        </template>
      </el-table-column>
      <el-table-column
        label=""
        prop="name">
      </el-table-column>
      <el-table-column
        label=""
        prop="phone">
      </el-table-column>
      <el-table-column
        label=""
        prop="address">
      </el-table-column>
    </el-table>
	<div style="width:100%;display:flex;align-items:center;justify-content:center;margin:10px 0">
		<el-button type="success" @click="addAddress"></el-button>
	</div>
    <el-descriptions title=""></el-descriptions>
    <el-table
      :data="tableData"
      style="width: 100%">
      <el-table-column
        label="">
        <template slot-scope="scope">
          <div class="shangpin">
            <el-image
              style="width: 100px; height: 100px"
              :src="baseUrl + scope.row.picture"
              fit="fill"></el-image>
            <span style="margin-left: 10px">{{ scope.row.goodname }}</span>
          </div>
        </template>
      </el-table-column>
      <el-table-column v-if="totalPrice>0"
        label="">
        <template slot-scope="scope">
          <span v-if="type == 2">{{ scope.row.price }} </span>
          <span v-else><span :style='{"fontSize":"12px"}'></span>{{ scope.row.price }}</span>
        </template>
      </el-table-column>
      <el-table-column v-if="seat==1"
        label="">
        <template slot-scope="scope">
          <span>{{ scope.row.activeSeat }}</span>
        </template>
      </el-table-column>
      <el-table-column v-if="seat!=1"
        label="">
        <template slot-scope="scope">
          <span>{{ scope.row.buynumber }}</span>
        </template>
      </el-table-column>
      <el-table-column v-if="totalPrice>0"
        label="">
        <template slot-scope="scope">
          <span v-if="type == 2">{{ scope.row.price * scope.row.buynumber }} </span>
          <span v-else-if="seat==1"><span :style='{"fontSize":"12px"}'></span>{{scope.row.total}}</span>
          <span v-else><span :style='{"fontSize":"12px"}'></span>{{ (scope.row.price * scope.row.buynumber).toFixed(2) }}</span>
        </template>
      </el-table-column>
    </el-table>
    <div style="width: 100%;padding: 12px;box-sizing: border-box;display: flex;align-items: center;">
        <span style="width: 80px; font-size: 14px;"></span><el-input type="textarea" v-model="remark" placeholder="" style="width: calc(100% - 80px);"></el-input>
    </div>
    <div class="buy" v-if="seat!=1 && tableData.length > 0">
      <template v-if="type == 2">
        <span style="color: red;margin-right: 10px;font-weight: 700;">{{totalPrice}} </span>
        <el-button type="warning" @click="jfClick"></el-button>
      </template>
      <template v-else>
        <span style="color: red;margin-right: 10px;font-weight: 700;"><span :style='{"fontSize":"12px"}'></span>{{totalPrice}}</span>
        <el-button type="success" @click="payClick"></el-button>
      </template>
    </div>
    <div class="buy" v-if="seat==1 && tableData.length > 0">
      <template v-if="type == 2">
        <span style="color: red;margin-right: 10px;font-weight: 700;">{{totalPrice}} </span>
        <el-button type="success" @click="xzJfClick"></el-button>
      </template>
      <template v-else>
        <span style="color: red;margin-right: 10px;font-weight: 700;" v-if="totalPrice>0"><span :style='{"fontSize":"12px"}'></span>{{totalPrice}}</span>
        <el-button type="success" @click="xzPayClick">{{totalPrice>0?'':''}}</el-button>
      </template>
    </div>
  </div>
</template>

<script>
import config from '@/config/config'
export default {
    data() {
		return {
			baseUrl: config.baseUrl,
			userTableName: localStorage.getItem('UserTableName'),
			addressList: [],
			tableData: [],
			currentRow: null,
			radio: -1,
			order: {
				address: "",
				buynumber: 0,
				consignee: '',
				discountprice: 0,
				discounttotal: 0,
				goodid: 0,
				goodname: "",
				shangjiahao : "",
				orderid: "",
				picture: "",
				price: 0,
				status: "",
				tablename: "",
				tel: "",
				total: 0,
				type: 1,
				userid: localStorage.getItem('userid')
			},
			user: {},
			type: 0,
			seat: 0,
			buynumber: 0,
			remark:'',
		}
    },
    created() {
		if(this.$route.query.type != undefined) {
			this.type = this.$route.query.type;
		}
		if(this.$route.query.seat != undefined) {
			this.seat = this.$route.query.seat;
		}
		if(this.$route.query.tablename != undefined) {
			this.order.tablename = this.$route.query.tablename;
		}
		this.getSession();
		this.getAddrList();
		// 
		var orderGoods = localStorage.getItem('orderGoods')
		// jsonlocalstoragestring
		this.tableData = JSON.parse(orderGoods);
		this.type = this.$route.query.type;
    },
    methods: {
		getSession() {
			if(!this.userTableName){
				this.$router.push('/login');
				return false
			}
			this.$http.get(`${this.userTableName}/session`, {emulateJSON: true}).then(res => {
				if (res.data.code == 0) {
					this.user = res.data.data;
				}
			});
		},
		getCurrentRow(index) {
			this.radio = index;
		},
		getAddrList() {
			this.$http.get('address/list', {params: {userid: localStorage.getItem('userid')}}).then(res => {
				if (res.data.code == 0) {
					this.addressList = res.data.data.list;
					this.addressList.forEach((item, index) => {
						if (item.isdefault == '') {
							this.radio = index;
						}
					});
				}
			});
		},
		addAddress(){
			this.$router.push('/index/shop-address/addOrUpdate');
		},
		getCartList() {
			this.$http.get('cart/list', {params: {page: 1, limit: 1000, userid: localStorage.getItem('userid')}}).then(res => {
				if (res.data.code == 0) {
					this.tableData = res.data.data.list;
					if (this.$route.query.id != undefined || this.$route.query.type != undefined) {
						this.tableData = [];
						this.buynumber = this.$route.query.buynumber;
						let detailObj = JSON.parse(this.$route.query.detailObj);
						this.order.tablename = this.$route.query.tablename;
						if (this.$route.query.type == 1) {
							this.tableData.push({
								buynumber: this.buynumber,
								goodid: detailObj.id,
								picture: this.$route.query.picture,
								goodname: this.$route.query.goodname,
								goodtype: this.$route.query.goodtype,
								tablename: this.$route.query.tablename,
								userid: localStorage.getItem('userid'),
								price: this.$route.query.price
							});
						}
						if (this.$route.query.type == 2) {
							this.tableData.push({
								buynumber: this.buynumber,
								goodid: detailObj.id,
								picture: this.$route.query.picture,
								goodname: this.$route.query.goodname,
								tablename: this.$route.query.tablename,
								userid: localStorage.getItem('userid'),
								jf: detailObj.jf
							});
						}
					}
				}
			});
		},
		handleChange(value, index, goodid) {
			this.$http.get(`${this.order.tablename}/detail/${goodid}`).then(res => {
				if (res.data.code == 0 && value > res.data.data.onelimittimes) {
					this.tableData[index].buynumber = --value;
					this.$message.error(`${res.data.data.onelimittimes}`);
				}
			});
		},
        // 
        payClick() {
			let that = this
            if (this.radio == -1) {
				this.$message({
					message: '',
					type: 'error',
					duration: 1500
				});
				return;
            }
			
            // 
            this.tableData.forEach((item,index) => {
                // 
                this.$http.get(`${item.tablename}/info/${item.goodid}`).then(res => {
                    // 
                    var orderId = Number(this.createOrder()) + Number(index)
                    let data = res.data.data;
                    data.alllimittimes = data.alllimittimes - item.buynumber;
                    // 
                    this.$http.post(`${item.tablename}/update`, data).then(res => {
                        // 
                        let order = {
                            orderid: orderId,
                            tablename: item.tablename,
                            userid: this.user.id,
                            goodid: item.goodid,
                            goodname: item.goodname,
                            shangjiahao: item.shangjiahao,
                            goodtype: item.goodtype,
                            picture: item.picture,
                            buynumber: item.buynumber,
                            discountprice: item.price,
                            price: item.price,
                            total: (item.price * item.buynumber).toFixed(2),
                            discounttotal: (item.price * item.buynumber).toFixed(2),
                            type: this.type?this.type:1,
                            //total: this.totalPrice,
                            address: this.addressList[this.radio].address,
                            tel: this.addressList[this.radio].phone,
                            consignee: this.addressList[this.radio].name,
                            remark: this.remark,
                            status: '',
							sfsh: '',
							role: this.userTableName,
                        }
                        this.$http.post('orders/add', order).then(res => {
                            // 
                            // 
                            if (Number(this.user.money) < Number(item.price * item.buynumber)) {
                                this.$message({
                                    message: '',
                                    type: 'error',
                                    duration: 1500,
								onClose(){
									that.$router.push('/index/center')
								}
                                });
                                return
                            }
                            // 
                            if (data.jf) {
                                this.user.jf = (Number(this.user.jf) + Number(item.price * item.buynumber)).toFixed(2)
                            }
                            this.user.money = (Number(this.user.money) - Number(item.price * item.buynumber)).toFixed(2)
                            // 
                            this.$http.post(`${this.userTableName}/update`, this.user).then(res => {
								localStorage.setItem('sessionForm',JSON.stringify(this.user))
                                order.status = ''
                                var params = {
                                    orderid: orderId,
                                    page: 1,
                                    limit: 1,
                                }
                                this.$http.get('orders/list', {params: params}).then(res => {
                                    order.id = res.data.data.list[0].id;
                                    this.$http.post(`orders/update`, order).then(res => {
                                        // ()
                                        if (item.id) {
                                            this.$http.post('cart/delete', [item.id]).then(res => {});
                                        }
                                        this.$message({
											message: '',
											type: 'success',
											duration: 1500,
											onClose: () => {
                                                this.$router.push('/index/shop-order/order');
											}
                                        });
                                    });
                                });
                            });
                        });
                    });
                });
            });
        },

        // 
        jfClick() {
            if (this.radio == -1) {
				this.$message({
					message: '',
					type: 'error',
					duration: 1500
				});
				return;
            }

            // 
            this.tableData.forEach((item,index) => {
                // 
                var orderId = Number(this.createOrder()) + Number(index)
                this.$http.get(`${item.tablename}/info/${item.goodid}`).then(res => {
                    let data = res.data.data;
                    // 
                    data.alllimittimes = data.alllimittimes - item.buynumber;
                    // 
                    this.$http.post(`${item.tablename}/update`, data).then(res => {
                        // 
                        if (this.user.jf < this.totalPrice) {
                            this.$message({
                                message: '',
                                type: 'error',
                                duration: 1500
                            });
                            return
                        }
                        // 
                        let order = {
                            orderid: orderId,
                            tablename: item.tablename,
                            userid: this.user.id,
                            goodid: item.goodid,
                            goodname: item.goodname,
                            shangjiahao: item.shangjiahao,
                            goodtype: item.goodtype,
                            picture: item.picture,
                            buynumber: item.buynumber,
                            discountprice: item.price,
                            price: item.price,
                            total: (item.price * item.buynumber).toFixed(2),
                            discounttotal: (item.price * item.buynumber).toFixed(2),
                            type: 2,
                            total: this.totalPrice,
                            address: this.addressList[this.radio].address,
                            tel: this.addressList[this.radio].phone,
                            consignee: this.addressList[this.radio].name,
                            status: '',
							sfsh: '',
							role: this.userTableName,
                        }
                        this.$http.post('orders/add', order).then(res => {
                            this.user.jf = (Number(this.user.jf) - Number(item.price * item.buynumber)).toFixed(2)
                            // 
                            this.$http.post(`${this.userTableName}/update`, this.user).then(res => {
								localStorage.setItem('sessionForm',JSON.stringify(this.user))
                                this.$message({
                                    message: '',
                                    type: 'success',
                                    duration: 1500,
                                    onClose: () => {
                                        this.$router.push('/index/shop-order/order');
                                    }
                                });
                            });
                        });
                    });
                });
            });
        },
        // ,
        xzPayClick() {
			let that = this
            // 
            this.tableData.forEach((item,index) => {
                // 
                var orderId = Number(this.createOrder()) + Number(index)
                // 
                this.$http.get(`${item.tablename}/info/${item.goodid}`).then(res => {
                    let data = res.data.data;
                    // 
                    data.alllimittimes = data.alllimittimes - item.buynumber;
                    // 
                    let selectArray = data.selected?data.selected.split(','):[];
                    // 
                    let activeArray = item.address.split(',');
                    // 
                    let newSelectArray = selectArray.concat(activeArray);
                    // 
                    data.selected = newSelectArray.join(',');
                    // ,
                    this.$http.post(`${item.tablename}/update`, data).then(res => {
                        // 
                        let order = {
                            orderid: orderId,
                            tablename: item.tablename,
                            userid: this.user.id,
                            goodid: item.goodid,
                            goodname: item.goodname,
                            shangjiahao: item.shangjiahao,
                            goodtype: item.goodtype,
                            picture: item.picture,
                            buynumber: item.buynumber,
                            discountprice: item.price,
                            price: item.price,
                            total: item.total,
                            discounttotal: item.total,
                            type: 1,
                            address: item.address,
                            remark: this.remark,
                            status: '',
							sfsh: '',
							role: this.userTableName,
                        }
                        this.$http.post('orders/add', order).then(res => {
                            // 
                            // 
                            if (Number(this.user.money) < Number(item.price * item.buynumber)) {
                                this.$message({
                                    message: '',
                                    type: 'error',
                                    duration: 1500,
									onClose(){
										that.$router.push('/index/center')
									}
                                });
                                return
                            }
                            // 
                            if (data.jf) {
                                this.user.jf = (Number(this.user.jf) + Number(item.price * item.buynumber)).toFixed(2)
                            }
                            this.user.money = (Number(this.user.money) - Number(item.price * item.buynumber)).toFixed(2)
                            // 
                            this.$http.post(`${this.userTableName}/update`, this.user).then(res => {
								localStorage.setItem('sessionForm',JSON.stringify(this.user))
                                order.status = ''
                                var params = {
                                    orderid: orderId,
                                    page: 1,
                                    limit: 1,
                                }
                                this.$http.get('orders/list', {params: params}).then(res => {
                                    order.id = res.data.data.list[0].id;
                                    this.$http.post(`orders/update`, order).then(res => {
                                        this.$message({
                                            message: `${this.totalPrice>0?'':''}`,
                                            type: 'success',
                                            duration: 1500,
                                            onClose: () => {
                                                this.$router.push('/index/shop-order/order');
                                            }
                                        });
                                    });
                                });
                            });
                        });
                    });
                });
            });
        },
		xzQRcodePaySuccess() {
			let that = this
            // 
            this.tableData.forEach((item,index) => {
                // 
                var orderId = Number(this.createOrder()) + Number(index)
                // 
                this.$http.get(`${item.tablename}/info/${item.goodid}`).then(res => {
                    let data = res.data.data;
                    // 
                    data.alllimittimes = data.alllimittimes - item.buynumber;
                    // 
                    let selectArray = data.selected?data.selected.split(','):[];
                    // 
                    let activeArray = item.address.split(',');
                    // 
                    let newSelectArray = selectArray.concat(activeArray);
                    // 
                    data.selected = newSelectArray.join(',');
                    // ,
                    this.$http.post(`${item.tablename}/update`, data).then(res => {
                        // 
                        let order = {
                            orderid: orderId,
                            tablename: item.tablename,
                            userid: this.user.id,
                            goodid: item.goodid,
                            goodname: item.goodname,
                            shangjiahao: item.shangjiahao,
                            goodtype: item.goodtype,
                            picture: item.picture,
                            buynumber: item.buynumber,
                            discountprice: item.price,
                            price: item.price,
                            total: item.total,
                            discounttotal: item.total,
                            type: 1,
                            address: item.address,
                            remark: this.remark,
                            status: '',
							sfsh: '',
							role: this.userTableName,
                        }
                        this.$http.post('orders/add', order).then(res => {
                            // 
                            // 
                            if (Number(this.user.money) < Number(item.price * item.buynumber)) {
                                this.$message({
                                    message: '',
                                    type: 'error',
                                    duration: 1500,
									onClose(){
										that.$router.push('/index/center')
									}
                                });
                                return
                            }
                            // 
                            if (data.jf) {
                                this.user.jf = (Number(this.user.jf) + Number(item.price * item.buynumber)).toFixed(2)
                            }
                            this.user.money = (Number(this.user.money) - Number(item.price * item.buynumber)).toFixed(2)
                            // 
                            this.$http.post(`${this.userTableName}/update`, this.user).then(res => {
								localStorage.setItem('sessionForm',JSON.stringify(this.user))
                                order.status = ''
                                var params = {
                                    orderid: orderId,
                                    page: 1,
                                    limit: 1,
                                }
                                this.$http.get('orders/list', {params: params}).then(res => {
                                    order.id = res.data.data.list[0].id;
                                    this.$http.post(`orders/update`, order).then(res => {
                                        this.$message({
                                            message: '',
                                            type: 'success',
                                            duration: 1500,
                                            onClose: () => {
                                                this.$router.push('/index/shop-order/order');
                                            }
                                        });
                                    });
                                });
                            });
                        });
                    });
                });
            });
        },
            //
        xzQRcodeClick(){
            this.QRcodePayVisible = true
        },
        
        // ,
        xzJfClick() {
            // 
            this.tableData.forEach((item,index) => {
                // 
                var orderId = Number(this.createOrder()) + Number(index)
                // 
                this.$http.get(`${item.tablename}/info/${item.goodid}`).then(res => {
                    let data = res.data.data;
                    // 
                    data.alllimittimes = data.alllimittimes - item.buynumber;
                    // 
                    let selectArray = data.selected?data.selected.split(','):[];
                    // 
                    let activeArray = item.address.split(',');
                    // 
                    let newSelectArray = selectArray.concat(activeArray);
                    // 
                    data.selected = newSelectArray.join(',');
                    // ,
                    this.$http.post(`${item.tablename}/update`, data).then(res => {
                        // 
                        let order = {
                            orderid: orderId,
                            tablename: item.tablename,
                            userid: this.user.id,
                            goodid: item.goodid,
                            goodname: item.goodname,
                            shangjiahao: item.shangjiahao,
                            goodtype: item.goodtype,
                            picture: item.picture,
                            buynumber: item.buynumber,
                            discountprice: item.price,
                            price: item.price,
                            total: (item.price * item.buynumber).toFixed(2),
                            discounttotal: 0,
                            type: 2,
                            address: item.address,
                            remark: this.remark,
                            status: '',
							sfsh: '',
							role: this.userTableName,
                        }
                        this.$http.post('orders/add', order).then(res => {
                            // 
                            // 
                            if (parseInt(this.user.jf) < parseInt(item.price * item.buynumber)) {
                                this.$message({
                                    message: '',
                                    type: 'error',
                                    duration: 1500
                                });
                                return
                            }
                            this.user.jf = (Number(this.user.jf) - Number(item.price * item.buynumber)).toFixed(2)
                            // 
                            this.$http.post(`${this.userTableName}/update`, this.user).then(res => {
								localStorage.setItem('sessionForm',JSON.stringify(this.user))
                                order.status = ''
                                var params = {
                                    orderid: orderId,
                                    page: 1,
                                    limit: 1,
                                }
                                this.$http.get('orders/list', {params: params}).then(res => {
                                    order.id = res.data.data.list[0].id;
                                    this.$http.post(`orders/update`, order).then(res => {
                                        this.$message({
                                            message: '',
                                            type: 'success',
                                            duration: 1500,
                                            onClose: () => {
                                                this.$router.push('/index/shop-order/order');
                                            }
                                        });
                                    });
                                });
                            });
                        });
                    });
                });
            });
        },
		createOrder() {
			let order = '';
			let now = new Date();
			order += now.getFullYear();
			order += now.getMonth() + 1;
			order += now.getDate();
			order += now.getHours();
			order += now.getMinutes();
			order += now.getSeconds();
			order += now.getMilliseconds();

			return order;
		}
    },
    computed: {
		totalPrice() {
			let totalPrice = 0;
			this.tableData.forEach(item => {
				totalPrice += item.price * item.buynumber;
			});
			return totalPrice.toFixed(2);
		},
		totalJifen() {
			let totalJifen = 0;
			this.tableData.forEach(item => {
				totalJifen += parseInt(item.jf * this.buynumber);
			});

			return totalJifen;
		}
    }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
